@import url(https://fonts.googleapis.com/css?family=Open+Sans:400|Raleway:300);
*{font-family:Raleway}body{height:100vh;background:radial-gradient(farthest-corner at 40% 0%, #f05 0%, #ee0979 100%)}.styled-btn{cursor:pointer;font-size:1.3rem;font-weight:light;color:white;padding:1rem 2.5rem;background-color:transparent;border:2px solid white;outline:none;transition:.09s all ease-in;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.styled-btn:hover{color:#ee0979;background-color:white}.display-none{display:none}

